import {Component, OnInit} from '@angular/core';
import {PolicyClass} from "../policy.class";
import {ActivatedRoute, Router} from "@angular/router";
import {PolicyService} from "../policy.service";

@Component({
  selector: 'app-limitconn',
  templateUrl: './limitconn.component.html',
  styleUrls: ['./limitconn.component.css']
})
export class LimitconnComponent implements OnInit {
  _id: string;
  datas: PolicyClass = new PolicyClass();

  limit_conn_action_list = [
    {'name': '', 'value': 'deny'},
    {'name': '', 'value': 'redirect'},
    {'name': '', 'value': 'reset_connection'},
    {'name': '', 'value': 'pass'},
    {'name': '', 'value': 'robot'},
  ];
  codeList = [
    {value: "400", text: "400"},
    {value: "401", text: "401"},
    {value: "403", text: "403"},
    {value: "404", text: "404"},
    {value: "405", text: "405"},
    {value: "410", text: "410"},
    {value: "500", text: "500"},
    {value: "501", text: "501"},
    {value: "503", text: "503"},
    {value: "504", text: "504"}
  ];
  constructor(
    private _router: Router,
    private _service: PolicyService,
    private _activatedRoute: ActivatedRoute) {
  }

  ngOnInit() {
    // 
    this._activatedRoute.params.subscribe((params) => {
      this._id = params['_id'];
      if (this._id) {
        this._service.getPolicy(this._id).subscribe(data => {
          this.datas.config = data['result'];
        })
      }
    });
  }

  setStateLimitState(value) {
    this.datas.config.twaf_limit_conn.state = value;
  }

  submit(form) {
    const that = this;
    this._service.setPolicy(this._id, this.datas).subscribe(
        res => {
          if (res['success']) {
            // this.toastr.success('', '');
            // 1s 
            setTimeout(function () {
              that._router.navigate(['pages', 'policy']); // that  this
            }, '1000');
          } else {
            // this.toastr.error('', '');
          }
        },
        error => {
            // this.toastr.error('', '');
        }
    )
  }
}
